@import 'assets/fonts/font_basil.scss';
@import 'assets/fonts/font_shrine.scss';

body {
    margin: 0;
    padding: 0;
}

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background-color: #B9E4C9;
}

::-webkit-scrollbar-thumb {
    background-color: #4ea98e;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #224b3e;
}

::-webkit-scrollbar-button {
    display: none;
}

/* fallback */
@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url(assets/icons/mat-icons.woff2) format('woff2');
}

.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    font-feature-settings: 'liga';
    -webkit-font-feature-settings: 'liga';
    -webkit-font-smoothing: antialiased;
}